//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: 600;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid rgba(0,0,0,.05);
  border: 0;
  border-bottom: 2px solid rgba(0,0,0,.2);
  box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.3);
  border-radius: 2px;
  white-space: nowrap;
  .button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; 1px);
  .user-select(none);


  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      .tab-focus();
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: @cursor-disabled;
    pointer-events: none; // Future-proof disabling of clicks
    .opacity(.65);
    .box-shadow(none);
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant(@btn-default-color; @btn-default-bg;);
  border-color: transparent;
}
.btn-primary {
  .button-variant(#fff; @biz-2;);
}
.btn-secondary {
  .button-variant(#fff; @biz-1;);
}
.btn-rss {
  .button-variant(#fff; #ff9900;);
}




.btn-facebook {
  .button-variant(#fff; @col-facebook;);
}
.btn-border {
  .button-variant(#fff; #333);
  border: 1px solid #fff;
}

// Danger and error appear as red
.btn-success {
  .button-variant(@btn-success-color; @btn-success-bg; );
}

// Danger and error appear as red
.btn-danger {
  .button-variant(@btn-danger-color; @btn-danger-bg; );
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: underline;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}

.form-oneline .btn {
  box-shadow: none;
}
// Button Sizes
// --------------------------------------------------

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; 1px);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  .button-size(5px; 14px; 13px; @line-height-small; 2px);
}
.btn-xs {
  .button-size(@padding-xs-vertical; 9px; @font-size-small; @line-height-small; 2px);
}

.btn-xxs {
  .button-size(0; 3px; 11px; 14px; 1px);
  font-weight: 600;
}



// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}
.btn-trans {
  font-size: 13px!important;
  font-weight: 600;
  line-height: 1.7;
}
  
